@import '../../../assets/scss/variables';
@import '../../../assets/scss/animations';

.introduction {
    background-image: linear-gradient( rgba(255, 255, 255, 1), rgba(255, 255, 255,.8)), url(../../../assets/img/background-aboutme-introduction.jpg);
    overflow: hidden;

    grid-row: 2 / 2;
    grid-column: 1 / 1;
    margin: 3rem;
    border-radius: 4rem;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, .1);

    animation-name: bounceEnter3;
    animation-duration: .8s;

    &:hover {
        box-shadow: 3px 3px 10px $color-primary;
    }
    
    @media only screen and (max-width: $bp-medium) {
        background-image: linear-gradient( rgba(255, 255, 255, 1), rgba(255, 255, 255,.8)), url(../../../assets/img/aboutme_background-phone.jpg);
    }
}